import { config } from "../utils/utils.js";
// –∫–ª–∞—Å—Å –≤–∑–∞–∏–º–æ–¥–µ–π—Å—Ç–≤–∏—è —Å —Å–µ—Ä–≤–µ—Ä–æ–º

export class Api {
  constructor({ url, headers = {} }) {
    this.url = url;
    this.headers = headers;
    this.authorization = headers.authorization;
  }

  // –æ–±—Ä–∞–±–æ—Ç–∫–∞ –æ—Ç–≤–µ—Ç–∞ —Å —Å–µ—Ä–≤–µ—Ä–∞

  _handleResponse(response) {
    if (response.ok) {
      return response.json();
    } else {
      return Promise.reject(`Oops, response is not ok. Status: ${response.status}`);
    }
  }

  // –æ–±—Ä–∞–±–æ—Ç–∫–∞ –æ—à–∏–±–æ–∫
  _handleError(error) {
    console.error(`Oops, an error occured: ${error}`)
    return Promise.reject(error.message)
  }

  // –ø–æ–ª—É—á–∏—Ç—å –ø—Ä–æ—Ñ–∏–ª—å —Å —Å–µ—Ä–≤–µ—Ä–∞
  getInfo() {
    return fetch(`${this.url}users/me`, { headers: this.headers })
      .then(response => this._handleResponse(response))
      .catch(this._handleError)
  }

  // –ø–æ–ª—É—á–∏—Ç—å –º–∞—Å—Å–∏–≤ –∏–∑–Ω–∞—á–∞–ª—å–Ω—ã—Ö –∫–∞—Ä—Ç–æ—á–µ–∫
  getInitialCards() {
    return fetch(`${this.url}cards`, { headers: this.headers })
      .then(response => this._handleResponse(response))
      .catch(this._handleError)
  }

  // –æ—Ç–ø—Ä–∞–≤–∏—Ç—å –æ–±–Ω–æ–≤–ª–µ–Ω–Ω—ã–π –ø—Ä–æ—Ñ–∏–ª—å —é–∑–µ—Ä–∞ –Ω–∞ —Å–µ—Ä–≤–µ—Ä
  editInfo(obj) {
    return fetch(`${this.url}users/me`,
      {
        method: 'PATCH',
        headers: this.headers,
        body: JSON.stringify({
          name: obj.name,
          about: obj.about
        })
      })
      .then(response => this._handleResponse(response))
      .catch(this._handleError)
  }

  // –æ—Ç–ø—Ä–∞–≤–∏—Ç—å –∫–∞—Ä—Ç–æ—á–∫—É –Ω–∞ —Å–µ—Ä–≤–µ—Ä
  sendElement(obj) {
    return fetch(`${this.url}cards`,
      {
        method: 'POST',
        headers: this.headers,
        body: JSON.stringify({
          name: obj.title,
          link: obj.url
        })
      })
      .then(response => this._handleResponse(response))
      .catch(this._handleError)
  }

  // —Å–º–µ–Ω–∏—Ç—å –∞–≤–∞—Ç–∞—Ä–∫—É –Ω–∞ —Å–µ—Ä–≤–µ—Ä–µ
  changeAvatar(obj) {
    return fetch(`${this.url}users/me/avatar`,
      {
        method: 'PATCH',
        headers: this.headers,
        body: JSON.stringify({
          avatar: obj.avatar
        })
      })
      .then(response => this._handleResponse(response))
      .catch(this._handleError)
  }

  // —É–¥–∞–ª–∏—Ç—å –∫–∞—Ä—Ç–æ—á–∫—É
  removeCard(_id) {
    return fetch(`${this.url}cards/${_id}`, {
      method: 'DELETE',
      headers: this.headers
    })
      .then(response => this._handleResponse(response))
      .catch(this._handleError)
  }

  // –ø–æ—Å—Ç–∞–≤–∏—Ç—å –ª–∞–π–∫
  likeCard(_id) {
    return fetch(`${this.url}cards/likes/${_id}`, {
      method: 'PUT',
      headers: this.headers
    })
      .then(response => this._handleResponse(response))
      .catch(this._handleError)
  }

  // —Å–Ω—è—Ç—å –ª–∞–π–∫
  dislikeCard(_id) {
    return fetch(`${this.url}cards/likes/${_id}`, {
      method: 'DELETE',
      headers: this.headers
    })
      .then(response => this._handleResponse(response))
      .catch(this._handleError)
  }
}

// —Å–æ–∑–¥–∞–µ–º —ç–∫–∑–µ–º–ø–ª—è—Ä Api
export const api = new Api(config);